import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Segoe UI', 'Roboto', arial, sans-serif;
  margin: 1em auto 0;
  padding: 1em;
  max-width: 768px;
  min-width: 560px;
`;

const Name = styled.h2`
  margin: 0;
`;
const Role = styled.h4`
  margin: 0;
`;
const Field = styled.div`
  padding: 0 .5em;
`;
const Links = styled.div`
  display: flex;
  font-size: 14px;
  ${Field}{
    border-right: 1px solid;
    &:last-of-type{border: none}
  }
`;
const Label = styled.label`
  font-weight: 700;
  margin-right: .25em;
`;
const Anchor = styled.a`
  color: ${({theme}) => theme.anchor};
  transition: opacity 200ms ease-in-out;
  font-weight: 700;
  &:link{
    opacity: 0.8;
  }
  &:visited, :active{
    opacity: 1;
  }
  &:hover{
    opacity: 1;
  }
`;
const Section = styled.section`
  display: flex;
  align-items: center;
  width: 100%;
  border-top: 1px solid;
  border-bottom: 1px solid;
  margin-top: 1em;
  font-size: 18px;
  font-weight: 700;
`;
const Company = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
`;

const BulletList = styled.ul`
  align-self: flex-start;
  list-style-position: outside;
  padding-inline-start: 2em;
  margin-block-start: 0px;
  margin-block-end: 0px;
  margin: 0;
  ${({noBullet}) => noBullet && 'list-style: none'};
  ${({size}) => size && `font-size: ${size}`};
`;
const Text = styled.p`
  margin: 0;
  font-size: ${({size}) => size || '12px'};
  font-weight: ${({bold}) => bold ? 700 : 500};
`;
const JobWrapper = styled.div`
  padding-left: 1em;
  width: 100%;
`;


const Resume = () => {
  return (
    <Wrapper>
      <Name>{'Kyle Lowe'}</Name>
      <Role>{'Senior Software Engineer'}</Role>
      <Links>
        <Field><Label>{'Phone:'}</Label>{'(913)744-5238'}</Field>
        <Field><Label>{'Email:'}</Label>{'Kyle.Lowe323@gmail.com'}</Field>
        <Field><Anchor href={'https://www.linkedin.com/in/kyle-lowe-64366057/'}>{'LinkedIn'}</Anchor></Field>
      </Links>
      <Section>{'Summary'}</Section>
      <BulletList>
        <li>{'I have 6+ years of experience developing business critical tools at the intersection of healthcare and Information Technology.'}</li>
        <li>{'Proficient in both client and server side web architectures.'}</li>
      </BulletList>
      <Section>{'Work Experience'}</Section>
      <Company>
        <Text size={'18px'} bold>{'Cerner Corporation'}</Text>
        <Text size={'18px'} bold>{'Kansas City, MO'}</Text>
      </Company>
      <JobWrapper>

      </JobWrapper>
      <Section>{'Technical Skills'}</Section>
      <BulletList>
        <li><Label>{'SCM:'}</Label>{'github, TFS'}</li>
        <li><Label>{'Languages:'}</Label>{'Javascript, HTML5, CSS3, C#, SQL, TSQL, VBA, XML,'}</li>
        <li><Label>{'Frameworks:'}</Label>{'React, Redux, Socket.io, Node, PM2, Husky, React Testing Library'}</li>
        <li><Label>{'OS:'}</Label>{'Windows, Linux'}</li>
        <li><Label>{'Software:'}</Label>{'Visual Studio, VSCode, Jenkins, Crucible, SSMS, Microsoft Office'}</li>
      </BulletList>
      <Section>{'Education'}</Section>
      <Company>
        <Text size={'14px'} bold>{'Johnson County Community College'}</Text>
        <Text size={'14px'} bold>{'Overland Park, KS'}</Text>
      </Company>
      <BulletList noBullet size={'14px'}>
        <li>{'Associate of Science Degree'}</li>
        <li>{'Computer Aided Drafting Certificate'}</li>
      </BulletList>
      <Company>
        <Text size={'14px'} bold>{'University of Kansas'}</Text>
        <Text size={'14px'} bold>{'Lawrence, KS'}</Text>
      </Company>
      <BulletList noBullet size={'14px'}>
        <li>{'School of Engineering'}</li>
        <li>{'Emphasis in Mechanical/Structural Engineering'}</li>
      </BulletList>
    </Wrapper>
  )
}

export default Resume;